



















/* @import './assets/css/base.css'; */
body{
    margin: 0;
    padding: 0;
}












































#tab-bar{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background-color: #f6f6f6;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    -webkit-box-shadow: 0 1px 1px rgba(100, 100, 100, .3);
            box-shadow: 0 1px 1px rgba(100, 100, 100, .3);
}



.tab-bar-item{
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
    /* 一般在開發時使用的高度 */
    height: 49px;
    font-size: 14px;
}
.tab-bar-item img{
    width: 24px;
    height: 24px;
    margin: 3px;
    margin-bottom: 2px;
    vertical-align: middle;
}

/* 改用props動態綁定 */
.active{
    color: red;
}


















































